
<script>
    export let items

    $: cartTotal = items.reduce((sum, item) => sum + item.price, 0)
</script>

<style>

    ul {
        margin: 1rem;
        padding: 0;
        list-style: none;
    }

    li {
        margin: 0.5rem 0;
        padding: 0.5rem;
        border: 1px solid #ccc;
    }

    h1 {
        font-size: 1rem;
        margin: 1rem;
    }
</style>


{#if items.length}
     <ul>
    {#each items as item}
        <li>{item.title}  -  ${item.price}</li>
    {/each}
    </ul>
{:else}
    <p>No items added to cart yet!</p>
{/if}



<h1>TOTAL: ${cartTotal}</h1>