<style>
    div {
        padding: 1rem;
        margin: 1rem;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
        border-radius: 5px;
        background: white;
    }

    h1 {
        font-size: 1.2rem;
        margin: 0.25rem 0; 
    }

    h2 {
        margin:0.25rem 0;
        font-size: 1rem;
        color: #4b4b4b;
        }

    p {
        margin: 0.25rem 0;
    }

</style>


<script>
    import Button from './Button.svelte'
    import { createEventDispatcher } from 'svelte'

    const dispatch = createEventDispatcher();

    function add () {
        dispatch('add', title)
    }

    export let title
    export let price
    export let desc

</script>



<div>
    <h1>{title}</h1>
    <h2>{price}</h2>
    <p>{desc}</p>
    <Button on:click={add}>Add to Cart</Button>
</div>